import React, { Component } from "react";
import TaskItem from "./TaskItem";

class TaskList extends Component {
  /**
   * 1 - temos que usar esa variável para manipular o estado.
   * 2 - o babel por padrao nao entende essas variaveis, ele espera que a gente coloque dentro e um constructor
   * para resolver isso, vamos instalar o yarn add @babel/plugin-proposal-class-properties -D.
   * 3 - Vamos no babel.config.js para add o plugin
   *  */
  state = {
    newTask: "",
    tasks: ["Planejar", "Estudar React", "Ler"]
  };

  /**
   * 1 - vamos criar um metodo para pegar o que o user digita em tempo real
   * 2 - Tem que ser Arrow functions, pois só assim temos acesso ou this.
   *  */
  handleInputChange = e => {
    this.setState({ newTask: e.target.value });
  };

  handleSubmit = e => {
    //para prevenir o reload completo da pagina
    e.preventDefault();

    /**
     * Quando manipulamos array ou obj, nao podemos modificar, mas sim recriar tudo.
     */
    this.setState({
      tasks: [...this.state.tasks, this.state.newTask],
      newTask: "" // para limpar o campo
    });
  };

  //deletando tasks
  handleDelete = task => {
    this.setState({ tasks: this.state.tasks.filter(t => t != task) });
  };

  //todos componente em forma de class tem que ter um render()
  render() {
    return (
      <>
        <h2>Olá, quais as tarefas de hj?</h2>
        <form onSubmit={this.handleSubmit}>
          <ul>
            {this.state.tasks.map(task => (
              <TaskItem
                key={task}
                task={task}
                onDelete={() => this.handleDelete(task)}
              />
            ))}
          </ul>
          <input
            type="text"
            onChange={this.handleInputChange}
            value={this.state.newTask}
            placeholder="Digite sua tarefa.."
          />
          <button type="submit">Enviar</button>
        </form>
      </>
    );
  }
}

export default TaskList;
